import React, { Component } from "react";
import "./homePage.css";
import Header from "./header";
import Menu from "./menu";
import "./menu.css";
import Table from "./table";
import Smile from "./images/smile.svg";
import Pet from "./images/pet.svg";
import Food from "./images/food.svg";
import Clothes from "./images/clothes.svg";
import Health from "./images/health.svg";
import Restoraunts from "./images/restor.svg";
import Transport from "./images/transport.svg";
import Utility from "./images/utility.svg";
import Other from "./images/other.svg";
import AddPage from "../add page/addPage";

let time = '';
let category = '';
class HomePage extends Component {
  state = {
    charges: [
      {
        fun: 30,
        pets: 65,
        food: 200.9,
        clothes: 154,
        health: 85,
        restoraunts: 86.57,
        transport: 51,
        bills: 134.5,
        other: 90,
      },
    ],
    dates: [
      {
        fun: new Date().toDateString(),
        pets: new Date().toDateString(),
        food: new Date().toDateString(),
        clothes: new Date().toDateString(),
        health: new Date().toDateString(),
        restoraunts: new Date().toDateString(),
        transport: new Date().toDateString(),
        bills: new Date().toDateString(),
        other: new Date().toDateString(),
      }
    ],

    sum: 0,
    titles: 4,
    icons: [],
    isActive: true,
    charge: 0,
    ch: 0,
  };

  componentDidMount() {
    this.sum();
    this.getIcons();
    this.eachTitle();
    this.showSideBar();
  }
  
  getIcons = () => {
    this.setState({
      icons: [
        Smile,
        Pet,
        Food,
        Clothes,
        Health,
        Restoraunts,
        Transport,
        Utility,
        Other,
      ],
    });
  };
  sum = () => {
    let sum = 0;
    this.state.charges.forEach((el) => {
      Object.values(el).map((e) => {
        sum += e;
      });
    });
    this.setState({
      sum: sum,
    });
  };
  eachCharge = () => {
    let arr = [];
    this.state.charges.map((el) => {
      arr = Object.values(el);
    });
    return arr;
  };
  eachTitle = () => {
    let arr = [];
    this.state.charges.map((el) => {
      arr = Object.keys(el);
    });
    this.setState({
      titles: arr,
    });
  };
  btnFunc = () => {
    this.homePageToggle();
    this.sum();
    this.saveDate();
  }
  homePageToggle = () => {
    this.setState({
      isActive: !this.state.isActive,
    });
    this.sum();
  };

  selectCategory = (e) => {
     category = e.target.value;
    this.setState((prevState) => ({
      charges: prevState.charges.map((obj, i) => {
        if (category === "fun") {
          return Object.assign(obj, {
            fun: prevState.charges[i].fun + this.state.ch,
          });
        } else if (category === "pets") {
          return Object.assign(obj, {
            pets: prevState.charges[i].pets + this.state.ch,
          });
        } else if (category === "food") {
          return Object.assign(obj, {
            food: prevState.charges[i].food + this.state.ch,
          });
        } else if (category === "clothes") {
          return Object.assign(obj, {
            clothes: prevState.charges[i].clothes + this.state.ch,
          });
        } else if (category === "health") {
          return Object.assign(obj, {
            health: prevState.charges[i].health + this.state.ch,
          });
        } else if (category === "restoraunts") {
          return Object.assign(obj, {
            restoraunts: prevState.charges[i].restoraunts + this.state.ch,
          });
        } else if (category === "transport") {
          return Object.assign(obj, {
            transport: prevState.charges[i].transport + this.state.ch,
          });
        } else if (category === "bills") {
          return Object.assign(obj, {
            bills: prevState.charges[i].bills + this.state.ch,
          });
        } else if (category === "other") {
          return Object.assign(obj, {
            other: prevState.charges[i].other + this.state.ch,
          });
        }
      }),
    }));
  };

  chargesState = (e) => {
    let newCharge = +e.target.value;
    this.setState({
      ch: newCharge,
    });
  };
  startToggle = () => {
    this.setState({
      landingPage: !this.state.landingPage,
    });
    this.showSideBar();
  };
  showSideBar = () => {
    let bar = document.querySelector(".side-manu");
    bar.style = "display: flex";
  };
  
  getDate = (date) =>{
    time = date.toDateString();
  }
saveDate = () =>{
  this.setState((prevState) => ({
    dates: prevState.dates.map((obj, i) => {
      if (category === "fun") {
        return Object.assign(obj, {
          fun: time,
        });
      } else if (category === "pets") {
        return Object.assign(obj, {
          pets: time,
        });
      } else if (category === "food") {
        return Object.assign(obj, {
          food: time,
        });
      } else if (category === "clothes") {
        return Object.assign(obj, {
          clothes: time,
        });
      } else if (category === "health") {
        return Object.assign(obj, {
          health: time,
        });
      } else if (category === "restoraunts") {
        return Object.assign(obj, {
          restoraunts: time,
        });
      } else if (category === "transport") {
        return Object.assign(obj, {
          transport: time,
        });
      } else if (category === "bills") {
        return Object.assign(obj, {
          bills: time,
        });
      } else if (category === "other") {
        return Object.assign(obj, {
          other: time,
        });
      }
    }),
  }));
}
  render() {
    return (
      <div className="home-page">
        <>
          {this.state.isActive && (
            <>
              <Header sum={this.state.sum} />
              <Menu btn={this.homePageToggle} />
              <div className="table-cont">
                {this.eachCharge().map((el, i) => (
                  <Table
                    charges={el}
                    title={this.state.titles[i]}
                    img={this.state.icons[i]}
                    date={this.state.dates.map((el) => {
                        return Object.values(el)[i];
                    })}
                  />
                ))}
              </div>
            </>
          )}
          {!this.state.isActive && (
            <AddPage
              sum={this.state.sum}
              btn={this.btnFunc}
              value={this.chargesState}
              select={this.selectCategory}
              date={this.getDate}
             
            />
          )}
        </>
      </div>
    );
  }
}
export default HomePage;
